import react, { useState } from "react";
import Card from "../UI/Card";
import userStyle from "../AddUser/AddUser.module.css";
import Button from "../UI/Button";
import Wrapper from "../Helper/Wrapper"
import ErrorModal from "../UI/ErrorModal";

const Adduser = (props) => {
  const [editUsername, setEditUserName] = useState("");
  const [editAge, setEditAge] = useState("");
  const [error, seterror] = useState();

  const userHandler = (event) => {
    event.preventDefault();
    if (editUsername.trim().length === 0 || editAge.trim().length === 0) {
      seterror({ title: "Invalid input", message: "Please enter valid input" });
      return;
    }

    if (+editAge < 1) {
      // '+' sign converts to integer
      seterror({ title: "Invalid age", message: "Please enter valid age(>0)" });
      return;
    }

    props.onAdduser(editUsername, editAge);
    setEditUserName("");
    setEditAge("");
  };

  const userNameHandler = (event) => {
    setEditUserName(event.target.value);
  };
  const AgeHandler = (event) => {
    setEditAge(event.target.value);
  };

  const errorHandl = () => {
    seterror(null);
  };

  return (
    <Wrapper>
      {error && (
        <ErrorModal
          title={error.title}
          message={error.message}
          onConfirm={errorHandl}
        />
      )}
      <Card className={userStyle.input}>
        <form>
          <label htmlFor="Username">Username</label>
          <input
            id="Username"
            type="text"
            value={editUsername}
            onChange={userNameHandler}
          />
          <label htmlFor="Age">Age (in year)</label>
          <input id="Age" type="number" value={editAge} onChange={AgeHandler} />

          <Button type="submit" onClick={userHandler}>
            Add User
          </Button>
        </form>
      </Card>
    </Wrapper>
  );
};

export default Adduser;
